.footer {
  background: #fff5f6;
  padding-top: 113px;
  padding-bottom: 25px;
  @include min768 {
    padding-bottom: 18px;
    padding-top: 165px;
  }
  @include min1280 {
    padding-bottom: 10px;
  }
}

.footer-slogan {
  @include min1280 {
    width: 100%;
  }
}

.container.down,
.footer-slogan {
  @include max767 {
    margin-bottom: 43px;
  }
}
.container.down {
  @include min1280 {
    margin-bottom: 97px;
  }
  @include min768 {
    margin-bottom: 91px;
    display: flex;
    // justify-content: space-between;
    // flex-wrap: wrap;
  }
  @include min768 {
    @include flex(flex, space-between, baseline);
  }
}

.footer__title {
  @include max767 {
    width: 232px;
  }
  align-items: flex-start;
  color: #fea5bb;
  font-family: Titan One;
  text-transform: uppercase;
  @include font(18px, 400, 1.16, null);

  @include min768 {
    margin-bottom: 15px;
    @include font(26px, 400, 1.14, null);
    width: 340px;
  }
  @include min1280 {
    width: 100%;
    @include font(30px, 400, 1.16, null);
  }
}
.footer__text {
  color: #d41443;
  font-family: Titan One;
  @include font(34px, 400, 1.14, null);
  text-transform: uppercase;
  margin-top: 17px;

  @include max767 {
    width: 200px;
  }
  align-items: flex-start;
  @include max1279 {
    margin-top: 17px;
  }
  @include min768 {
    margin-bottom: 15px;
    @include font(48px, 400, 1.14, null);
  }
  @include min1280 {
    @include font(58px, 400, 1.14, null);
  }
}

.footer__icon {
  fill: var(--main-white-color);
  background-color: #fea5bb;
  border-radius: 50%;
  transition: fill 250ms linear, background-color 250ms linear;
}
.footer-social__link {
  border-radius: 50%;
  &:hover .footer__icon,
  &:focus .footer__icon {
    fill: var(--main-white-color);
    background-color: var(--accent-color);
  }
}

.footer-social__list {
  margin-bottom: 46px;
  @include flex(flex, flex-end, null);
  @include min768 {
    margin-bottom: 50px;
  }
  @include min768-max1279 {
    margin-left: 150px;
  }

  @include min1280 {
    margin-left: 189px;
  }
  .footer-social__item:not(:last-child) {
    margin-right: 24px;
  }
}
.footer__contact {
  @include max767 {
    text-align: end;
    margin-left: 50px;
  }
}
.footer__media {
  @include min768 {
    @include flex(flex, space-between, baseline);
    flex-wrap: wrap;
    // margin-left: 92px;
  }
  @include min1280 {
    @include flex(flex, space-between, baseline);
    margin-left: 257px;
    align-items: flex-end;
  }
}

.footer__contact--text,
.footer__link--tel {
  font-family: DM Sans;
  @include font(12px, 700, 1.86, null);
  @include min768 {
    @include font(14px, 700, 1.86, null);
  }
  @include min1280 {
    @include font(16px, 700, 1.86, null);
  }
}
.footer__contact--text {
  color: #000000;
}
.footer__link--tel {
  color: #d41443;
}
.copyright {
  display: block;
  border-top: 1px solid #e1e1e1;
  text-align: center;
}
.copyright__title {
  color: #907e82;
  font-family: DM Sans;
  @include font(14px, 700, 1.52, null);
  padding-top: 9px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  @include max767 {
    width: 150px;
  }
  @include min768 {
    width: 100%;
  }
}
